
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import {mapMutations} from 'vuex';
export default {
name:'login',
components:{
XInput,
Group,
XButton,
Toast,
Msg
},
data:function(){
return {
accesstoken:'',
isShowToast:false,
toastText:'',
isShowMsg:false,
isShow:true,
buttons:[
{
type:'primary',
text:'',
link:'/center'
}
]
}

},
methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationAuthorId']),
dologin:function(){

var app=this;
app.$http.post('/accesstoken/',{
accesstoken:this.accesstoken
}).then(function(response){


if(response.data.success){
app.mutationLogin(true);
app.mutationLoginname(response.data.loginname);
app.mutationAccessToken(app.accesstoken);
app.mutationAuthorId(response.data.id);


window.localStorage.setItem('loginStatus',true);

window.localStorage.setItem('loginname',response.data.loginname);

window.localStorage.setItem('accesstoken',app.accesstoken)

window.localStorage.setItem('authorId',response.data.id)

app.$router.push('/user/'+response.data.loginname)
}
}).catch(function(error){


app.isShowToast=true;
app.toastText=error.response.data.error_msg;
})
}
},
created:function(){

if(true==this.$store.state.isLogined){
this.isShowMsg=true;
this.isShow=false
return;
}
}
}



